const hello = () => {
    console.log("Hello");
}

const codeSource = document.getElementsByClassName('code-source')[0];

codeSource.addEventListener('keyup', (e) => {
    console.log("LOGO : " + codeSource.innerText);
    console.log(e);
})

codeSource.getElementsByTagName('code')[0].addEventListener('keydown', function onKeyDown(e) {
    if (e.keyCode === 9) { // tab key
        e.preventDefault();
        codeSource.getElementsByTagName('code')[0].appendChild(document.createTextNode('dfsfsd'))
    }
    
})



